export function tlRotateIcon() {
	gsap.to('._rotate-el-01', {
		rotate: -720, //  
		ease: 'none', //    
		scrollTrigger: {
			trigger: '._rotate-el-01',
			start: 'top bottom', //  ,  `.trigger`  
													 //  
			end: 'top top', //  ,  `.trigger`   
											// 
			scrub: true, //       
			// markers: true, //   (  )
		}
	});
	gsap.to('._rotate-el-02', {
		rotate: -720, //  
		ease: 'none', //    
		scrollTrigger: {
			trigger: '._rotate-el-02',
			start: 'top bottom', //  ,  `.trigger`  
													 //  
			end: 'top top', //  ,  `.trigger`   
											// 
			scrub: true, //       
			// markers: true, //   (  )
		}
	});
}

